import React from "react";
import Grid from '@material-ui/core/Grid';
import Selector from "../Selector/index.js";
import "./index.css";

export default class ColourSelector extends React.PureComponent {
  constructor(props) {
    super(props);
    this.colours = ["#F94144", "#F3722C", "#F9C74F", "#90BE6D", "#6F84EF", "#626262"];
  }

  render() {
    var colourButtons = [];
    for (let i = 0; i < this.colours.length; i++) {
      colourButtons.push(<Selector onClick={this.props.onSelect} colour={this.colours[i]}/>);
    }

    return (
      <Grid className="colour-selectors" container spacing={1}>
        {colourButtons}
      </Grid>
    );
  }
}
