import React from "react";
import Grid from '@material-ui/core/Grid';
import { Title, UnderpopulationSVG, ContinuationSVG, OverpopulationSVG, ReproductionSVG } from "./variables.js";
import "./index.css";

export default class Instructions extends React.Component {
  render() { 
    return (
      <div className="instructions">
        <Grid container spacing={2} >
          <Grid item xs={12}>
            <h3>Underpopulation</h3>
            <p>Any live cell with fewer than two live neighbours dies, as if by underpopulation.</p>
            {UnderpopulationSVG}
          </Grid>
          <Grid item xs={12}>
            <h3>Continuation</h3>
            <p>Any live cell with two or three live neighbours lives on to the next generation.</p>
            {ContinuationSVG}
          </Grid>
          <Grid item xs={12}>
            <h3>Overpopulation</h3>
            <p>Any live cell with more than three live neighbours dies, as if by overpopulation.</p>
            {OverpopulationSVG}
          </Grid>
          <Grid item xs={12}>
            <h3>Reproduction</h3>
            <p>Any dead cell with three live neighbours becomes a live cell, as if by reproduction.</p>
            {ReproductionSVG}
          </Grid>
        </Grid>
      </div>
    );
  }
}
