import React from "react";
import Game from "../Game/index.js";
import ColourSelector from "../ColourSelector/index.js";
import Instructions from "../Instructions/index.js";
import { Title } from "../Instructions/variables.js";
import Grid from "@material-ui/core/Grid";
import "./index.css";

export default class Page extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      theme: "#6f84ef",
    };

    this.onSelect = this.onSelect.bind(this);
  }

  onSelect(colour) {
    this.setState({ theme: colour });
  }

  render() {
    return (
      <div class="page">
        <Title className="title" colour={this.state.theme} />
        <ColourSelector onSelect={this.onSelect}/>
        <Game theme={this.state.theme} />
        <Instructions theme={this.state.theme} />
      </div>
    );
  }
}
