import { withStyles } from "@material-ui/core/styles";
import Slider from "@material-ui/core/Slider";

const SpeedSlider = withStyles({
  root: {
    color: props => props.colour != null ? props.colour : "#6f84ef",
    height: 6,
    alignItems: "center",
  },
  thumb: {
    height: 12,
    width: 12,
    marginTop: -3,
    marginLeft: -6,
    "&:focus, &:hover, &$active": {
      boxShadow: "inherit",
    },
  },
  active: {},
  track: {
    height: 6,
    borderRadius: 4,
  },
  rail: {
    height: 6,
    borderRadius: 4,
  },
})(Slider);

export default SpeedSlider;