import React from "react";
import "./index.css";

export default class Square extends React.PureComponent {
  render() {
    let backgroundColour;
    switch (this.props.value) {
      case 0:
        backgroundColour = "#FBFBFB";
        break;
      case 1:
        backgroundColour = this.props.colour;
        break;
      case 2:
        backgroundColour = "#CBCBCB";
        break;
    }

    return (
      <div
        className="square"
        onClick={() => this.props.onClick(this.props.row, this.props.col)}
        style={{ backgroundColor: backgroundColour }}
      />
    );
  }
}
